import getData from "./getData.js";
import renderCart from "./renderCart.js";

const cart = () => {
    const cartBtn = document.getElementById('cart');
    const cartModal = document.querySelector('.cart');
    const cartModalCloseBtn = cartModal && cartModal.querySelector('.cart-close');
    const goodsWrapper = document.querySelector('.goods');
    const counterSpan = document.querySelector('#cart .counter') || document.querySelector('.counter');

    const updateCounter = () => {
        const cart = JSON.parse(localStorage.getItem('cart')) || [];
        if (counterSpan) counterSpan.textContent = cart.length;
    };

    const openCart = () => {
        const cart = JSON.parse(localStorage.getItem('cart')) || [];
        cartModal.style.display = 'flex';
        renderCart(cart);
        updateCounter();
    };

    const closeCart = () => {
        cartModal.style.display = '';
    };

    if (cartBtn) cartBtn.addEventListener('click', openCart);
    if (cartModalCloseBtn) cartModalCloseBtn.addEventListener('click', closeCart);

    if (goodsWrapper) {
        goodsWrapper.addEventListener('click', async (event) => {
            const btn = event.target.closest('button');
            if (!btn) return;

            if (!btn.classList.contains('btn-primary')) return;

            const card = btn.closest('.card');
            if (!card) {
                console.warn('card not found for clicked button', event.target);
                return;
            }

            const key = card.dataset.key;
            if (!key) {
                console.warn('data-key missing on card', card);
                return;
            }

            let goods = JSON.parse(localStorage.getItem('goods'));
            if (!Array.isArray(goods) || goods.length === 0) {
                try {
                    goods = await getData();
                    localStorage.setItem('goods', JSON.stringify(goods));
                } catch (err) {
                    console.error('Failed to fetch goods', err);
                    return;
                }
            }

            const goodItem = goods.find(item => String(item.id) === String(key) || item.id === +key);
            if (!goodItem) {
                console.warn('Item not found in goods for key', key, goods);
                return;
            }

            const cartArr = JSON.parse(localStorage.getItem('cart')) || [];

            cartArr.push(goodItem);
            localStorage.setItem('cart', JSON.stringify(cartArr));

            updateCounter();
            if (cartModal.style.display === 'flex') renderCart(cartArr);
            console.log('Added to cart:', goodItem);
        });
    } else {
        console.warn('goods wrapper not found (.goods)');
    }

    updateCounter();
};

export default cart;
